.Dot {
  -webkit-transition: stroke-opacity 1s, stroke-width 1s;
  -o-transition: stroke-opacity 1s, stroke-width 1s;
  transition: stroke-opacity 1s, stroke-width 1s;
}

.TheirTurn {
  cursor: not-allowed;
}

.OurTurn:hover {
  stroke-opacity: 0;
  stroke-width: 0;
  cursor: -webkit-grab;
  cursor: grab;
}

.Selected {
  stroke-width: 0;
}

.flash:hover {
  -webkit-animation: shake 1s linear;
          animation: shake 1s linear;
}

.flash {
  cursor: not-allowed;
}



