.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.Header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.Header-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  height: 100%;
  width: 100%;
}

.App-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  height: 100%;
  width: 100%;

  background-size: 50px 50px;
  /* background-image: linear-gradient(to right, cyan 2px, transparent 1px), linear-gradient(to bottom, cyan 1px, transparent 1px); */
  background-image: -webkit-radial-gradient(
    circle,
    #61d60914 2px,
    rgba(0, 0, 0, 0) 55px
  );
  background-image: -o-radial-gradient(
    circle,
    #61d60914 2px,
    rgba(0, 0, 0, 0) 55px
  );
  background-image: radial-gradient(
    circle,
    #61d60914 2px,
    rgba(0, 0, 0, 0) 55px
  );
}

.LeftPanel {
}

.CentrePanel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.RightPanel {
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Recognition {
  background-color: #f3efd6;
  font-family: fantasy;
  font-size: 10px;
  opacity: 0.6;
}

.Author {
  background-color: #f3efd6;
  font-family: fantasy;
  font-size: 12px;
}

.Status {
  margin-bottom: 10px;
}



